.Registration {
  display: flex;
  width: 100%;
  height: 100vh;

  .aside {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35%;
    height: 100%;
    background-color: #000;
  }

  .main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    height: 100%;
    background: url('../../assets/map-bg.png') center center no-repeat;
    background-size: cover;

    .formWrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 2rem;
      align-items: center;
      width: 24rem;
      height: 25rem;
      background: #ffffff;
      box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
      border-radius: 20px;

      .form {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        gap: 0.5rem;
        width: 100%;

        .link {
          font-size: 0.8rem;
          color: #828282;

          & > button {
            background-color: transparent;
            border: none;
            color: #fdbf5a;
            text-decoration: none;
            font-size: 0.8rem;

            &:hover {
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
